import React from 'react'
import { Form, FormGroup, Label, Input } from 'reactstrap';
import { appAddOns } from '../data/business-services/index';
export const SelectAddons = () => {
    return (
      <div>
        <Form>
          <FormGroup check inline>
            <Label check>
              <Input type="checkbox" /> Some input
            </Label>
          </FormGroup>
          
        </Form>
        {productConsumer.activePackage.map((service, index) => (
          <CardComp
            key={index}
            title={service.title}
            text={service.content}
            addOns={service.addOns}
            image={service.src}
            package={service}
          />
        ))}
      </div>
    );
}
